  bottom: 0;
  background-color: #ccc;
  border-radius: 50px;
  transition: 0.4s;
}
.slider::before {
  position: absolute;
  content: "";
  height: 23px;
  width: 25px;
  border-radius: 50px;
  left: 4px;
  bottom: 4px;
  background-color: #fff;
  transition: 0.4s;
}
